import React from 'react';
import { Modal, Icon, Button } from 'react-materialize';
import logo from '../../img/logo7.png';

class Showcase extends React.Component {
  constructor() {
    super();
    this.showModal = this.showModal.bind(this);
    this.id = 'yo';
  }

  showModal() {
    $(`#${this.id}`).modal('open');
  }
  render() {
    return (
      <div className="showcase container">
        <div className="row">
          <div className="col s12 m10 offset-m1 center">
            <img src={logo} height="200px" />
            <h4>EKKAWIT LERTKRAI</h4>
            <p>
              ติดต่อสอบถามการจ้างงานได้ที่นี้ สามารถโหลด resume ได้ข้างล่างนี้
            </p>
            <br />
            <a
              href="#contact-modal"
              className="btn waves-effect waves-light  white black-text modal-trigger"
            >
              Contract
            </a>
            <a
              href="resume.html"
              className="btn waves-effect waves-light black white-text"
            >
              Resume
            </a>
          </div>
        </div>
        <SideNav
          trigger={<Button>SIDE NAV DEMO</Button>}
          options={{ closeOnClick: true }}
        >
          <SideNavItem
            userView
            user={{
              background: 'img/office.jpg',
              image: 'img/yuna.jpg',
              name: 'John Doe',
              email: 'jdandturk@gmail.com'
            }}
          />
          <SideNavItem href="#!icon" icon="cloud">
            First Link With Icon
          </SideNavItem>
          <SideNavItem href="#!second">Second Link</SideNavItem>
          <SideNavItem divider />
          <SideNavItem subheader>Subheader</SideNavItem>
          <SideNavItem waves href="#!third">
            Third Link With Waves
          </SideNavItem>
        </SideNav>
      </div>
    );
  }
}

export default Showcase;
